.nav {
    position: fixed;
    top:0px; 
    padding: 20px;
    width:100%;
    height:60px;
    z-index:1;
    box-sizing: border-box;
    transition: all 0.5s ease-in 0s;
}

.nav__content {
    display: flex;
    justify-content: space-between;
}

.nav__content .logo {
    width: 100px;
    cursor:pointer;
    top:10px;
    left:20px;
}

.nav__avatar {
    width: 30px;
}

.nav__black {
    background-color: #000;
}